.aboutContainer {
  max-width: 1000px;
  margin:auto;
  width:95%;
} 

.aboutTextContainer {
  width:50%;
  color:white;
  text-align: left;
  display:inline-block;
  h1 {
    font-size:48px;
  }
  h2 {
    font-size:16px;
    margin-top:-25px;
    margin-bottom:30px;
  }
  p {
    line-height: 28px;
    text-align: left;
  }
  .divider {
    height:1px;
    background:white;
    width:80%;
    margin-top:-10px;
    margin-bottom:40px;
  }
}

.aboutImgContainer {
  width:50%;
  display:inline-block;
  img {
    width:100%;
    opacity: 0.8;
    transform:translateX(20px);
  }
}


.aboutContainer2 {
  max-width: 1000px;
  margin:auto;
  margin-top:10vh;
  width:95%;
  text-align:center;
  color:white;
  animation: fade 1s;
} 

.aboutTextContainer2 {
  max-width: 800px;
  margin:auto;
  margin-top:40px;
  line-height:30px;
  color:lightgray;
  width:95%;
  margin-bottom:50px;
}

.aboutImgContainer2 {
  width:200px;
  height:200px;
  display: inline-block;
  img {
    width:100%;
    height:100%;
    display:inline-block;
    border-radius: 360px;
  }
}

.titleContainer {
  display: inline-block;
  vertical-align: top;
  margin-top:30px;
  margin-left:30px;
  h1 {
    font-size:42px;
  }
  h2 {
    font-size:16px;
    margin-top:-25px;
    margin-bottom:30px;
  }
}

@keyframes fade {
  from {opacity: 0;}
  to {opacity: 1;}
}


@media only screen and (max-width: 480px) {

  .aboutImgContainer2 {
    margin-top:-20px;
    width:175px;
    height:175px;
    display: inline-block;
    img {
      width:100%;
      height:100%;
      display:inline-block;
      border-radius: 360px;
    }
  }

  .titleContainer {
    display: inline-block;
    vertical-align: top;
    margin:auto;
    margin-top:10px;
    h1 {
      font-size:36px;
    }
    h2 {
      font-size:16px;
      margin-top:-15px;
      margin-bottom:30px;
    }
  }

  .aboutTextContainer2 {
    font-size:14px;
  }
  
  
}
